@import url(https://fonts.googleapis.com/css2?family=Zilla+Slab+Highlight:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@300&display=swap);
body{
  text-align:center;
}
.title{
  font-size:50px;
  text-align: center;
  font-family: 'Zilla Slab Highlight', cursive;
  color:  #efe3d7 ;
  
}
.para{
  font-family: 'Zilla Slab Highlight', cursive;
  color:  #efe3d7 ;
  line-height:5px;
}

.html,
body {
  background-color:   #e3bd99  ;
}

.App {
  font-family: 'Work Sans', sans-serif;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-align-content: center;
          align-content: center;
  padding-top: 5px;
  font-size: calc(10px + 2vmin);
  color: white;
  text-align: center;
}

.cookie-button {
  width: 150px;
  height: 150px;
  background: url(/react-clicker/static/media/cookie.b414d3ef.png) center center;
  background-size: cover;
  cursor: pointer;
}

.cookie-button:active {
  width: 140px;
  height: 140px;
  margin: 10px;
}

.currency {
  margin: 20px 0 80px 0;
}

.menu {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  width: 600px;
  height: 150px;
  background-color:   #efe3d7  ;
  margin: 25px;
  border-radius: 25px;
}

.store-item {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 100px;
  height: 100px;
  background-color:  #e9d4c0 ;
  box-shadow: 0 2px 4px -2px rgba(0, 0, 0, 0.7);
  border-radius: 100px;
  margin: 25px 15px;
  cursor: pointer;
}

.store-item:active {
  width: 96px;
  height: 96px;
  border: dashed 2px rgb(255, 215, 64, 0.1);
  background-color: rgba(0, 0, 0, 0.25);
  box-shadow: none;
}

.icon-price {
  display: inline-block;
  width: 18px;
  height: 18px;
  background: url(/react-clicker/static/media/coin.4171edf0.PNG)
    center center;
  background-size: cover;
}
